<template>
  <a-menu
    class="a_menu"
    mode="inline"
    :selectedKeys.sync="selectedKeys"
    :inlineIndent="12"
    @click="handleClick"
  >
    <template v-for="item in menuData">
      <a-menu-item
        v-if="!Array.isArray(item.children) || item.children.length===0"
        :key="item.id"
        :value="item"
      >
        <a-icon type="pie-chart" />
        <span>{{ item.title }}</span>
      </a-menu-item>
      <SubMenu v-else :key="item.id" :menu-info="item" />
    </template>
  </a-menu>
</template>
<script>
import { Menu } from "ant-design-vue";
const SubMenu = {
  template: `
    <a-sub-menu :key="menuInfo.id" v-bind="$props" v-on="$listeners">
      <span slot="title">
        <a-icon type="mail" /><span>{{ menuInfo.title }}</span>
      </span>
      <template v-for="item in menuInfo.children">
        <a-menu-item v-if="!Array.isArray(item.children) || item.children.length===0" :key="item.id" :value="item">
          <a-icon type="pie-chart" />
          <span>{{ item.title }}</span>
        </a-menu-item>
        <SubMenu v-else :key="item.id" :menu-info="item" />
      </template>
    </a-sub-menu>
  `,
  name: "SubMenu",
  isSubMenu: true,
  props: {
    ...Menu.SubMenu.props,
    menuInfo: {
      type: Object,
      default: () => ({}),
    },
  },
};
export default {
  props: {
    menuData: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      selectedKeys: [],
    };
  },
  methods: {
    handleClick({ item }) {
      console.log("item.path", item.value);
      this.$router.push(item.value.path);
    },
  },
  watch: {
    $route() {
      console.log("$route----", this.$route, this);
      this.selectedKeys = [this.$route.meta.id];
    },
  },
  created() {
    this.$router.on;
  },
  components: {
    SubMenu,
  },
};
</script>
<style lang="less" scoped >
.a_menu {
  width: 100%;
  height: 100%;
  text-align: left;
}
</style>